.hide {
  display: none;
}
.show {
  display: block;
}
.clear {
  *zoom: 1;
}
.clear:after {
  display: table;
  content: '';
  clear: both;
}
.left {
  float: left;
}
.right {
  float: right;
}
.auto {
  margin-left: auto;
  margin-right: auto;
}
.zx10 {
  z-index: 10;
}
.zx20 {
  z-index: 20;
}
.ell {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.ellis {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.icon {
  width: 0.5rem;
  height: 0.5rem;
  vertical-align: -0.15rem;
  fill: currentColor;
  color: #fff;
  overflow: hidden;
}
.router-fade-enter-active,
.router-fade-leave-active {
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}
.router-fade-enter,
.router-fade-leave-active {
  opacity: 0;
}
